<template>
  <div class="character-select">
    <div class="stage">
      <!-- Ï¢åÏ∏°: Í≥†ÏñëÏù¥ ÏòÅÏó≠ Î≤ÑÌäº -->
      <button
        class="zone left"
        :class="{ highlight: currentHighlight === 'cat', selected: result === 'cat' }"
        @click="pick('cat')"
        :disabled="isRolling"
        aria-label="Í≥†ÏñëÏù¥ ÏÑ†ÌÉù"
      />

      <!-- Ïö∞Ï∏°: ÏÑ†ÏÉùÎãò ÏòÅÏó≠ Î≤ÑÌäº -->
      <button
        class="zone right"
        :class="{ highlight: currentHighlight === 'teacher', selected: result === 'teacher' }"
        @click="pick('teacher')"
        :disabled="isRolling"
        aria-label="ÏÑ†ÏÉùÎãò ÏÑ†ÌÉù"
      />

      <!-- ÌïòÎã®: ÏãúÏûë Î≤ÑÌäº -->
      <button class="start-btn" @click="startRoll" :disabled="isRolling">
        ÎûúÎç§ ÏÑ†ÌÉù
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, onBeforeUnmount } from 'vue'
import { useRouter } from 'vue-router'
import { useSchoolStore } from '@/stores/school'

const router = useRouter()
const school = useSchoolStore()

const isRolling = ref(false)
const currentHighlight = ref('cat')   // 'cat' | 'teacher'
const result = ref(null)               // ÏµúÏ¢Ö Í≤∞Í≥º

let intervalId = null
let timeoutId = null

const startRoll = () => {
  if (isRolling.value) return
  isRolling.value = true
  result.value = null

  // 150ms Í∞ÑÍ≤©ÏúºÎ°ú ÌïòÏù¥ÎùºÏù¥Ìä∏ Î≤àÍ∞àÏïÑÍ∞ÄÎ©∞
  intervalId = setInterval(() => {
    currentHighlight.value = currentHighlight.value === 'cat' ? 'teacher' : 'cat'
  }, 150)

  // 3Ï¥à ÌõÑ ÌôïÏ†ï
  timeoutId = setTimeout(() => {
    clearInterval(intervalId)
    intervalId = null
    const pick = Math.random() < 0.5 ? 'cat' : 'teacher'
    result.value = pick
    currentHighlight.value = pick

    // store Ï†ÄÏû• ÌõÑ Í≤åÏûÑÏúºÎ°ú Ïù¥Îèô
    school.setCharacter(pick)
    setTimeout(() => router.push('/game'), 600)
    isRolling.value = false
  }, 3000)
}

const pick = (type) => {
  // ÏÇ¨Ïö©ÏûêÍ∞Ä ÏßÅÏ†ë ÎàÑÎ•¥Î©¥ Ï¶âÏãú ÌôïÏ†ï
  if (isRolling.value) return
  result.value = type
  currentHighlight.value = type
  school.setCharacter(type)
  router.push('/game')
}

onBeforeUnmount(() => {
  if (intervalId) clearInterval(intervalId)
  if (timeoutId) clearTimeout(timeoutId)
})
</script>

<style scoped>
.character-select {
  height: 100vh;
  display: grid;
  place-items: center;
  background: #fffafc;
}

.stage {
  position: relative;
  width: min(1000px, 92vw);
  aspect-ratio: 16 / 10;
  background: url('@/assets/bg_2.png') no-repeat center / cover;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0,0,0,.15);
  overflow: hidden;
}

/* Ï¢å/Ïö∞ ÌÅ¥Î¶≠ ÏòÅÏó≠ */
.zone {
  position: absolute;
  top: 18%;
  bottom: 26%;
  width: 400px;
  height:400px;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: box-shadow .2s, transform .2s, outline .2s;
  outline: none;
  opacity:0.3;
}

/* Ï¢åÏ∏°(Í≥†ÏñëÏù¥) */
.zone.left {
  left: 8%;
  background: url('@/assets/img_char_cat.png');
  background-size: contain;
}

/* Ïö∞Ï∏°(ÏÑ†ÏÉùÎãò) */
.zone.right {
  right: 17%;
    background: url('@/assets/img_char_teacher.png');
  background-size: contain;
}

/* Î≤àÍ∞àÏïÑ ÌïòÏù¥ÎùºÏù¥Ìä∏ */
.zone.highlight {
  opacity: 1;
  animation: pulse .6s ease-in-out infinite alternate;
}

/* ÌôïÏ†ï ÌëúÏãú */
.zone.selected {
  transform: scale(1.1);
}

/* ÏãúÏûë Î≤ÑÌäº */
.start-btn {
  position: absolute;
  left: 50%;
  bottom: 6%;
  transform: translateX(-50%);
  padding: 12px 48px;
  border: none;
  border-radius: 14px;
  font-weight: 800;
  font-size: 1.2rem;
  color: #fff;
  background: linear-gradient(135deg, #ff6b81, #9b5de5);
  cursor: pointer;
  transition: transform .15s, box-shadow .2s;
}
.start-btn:hover { transform: translateX(-50%) scale(1.03) }
.start-btn:active { transform: translateX(-50%) scale(.95) }
.start-btn:disabled { opacity: .7; cursor: not-allowed }

@keyframes pulse {
  from { filter: drop-shadow(0 0 0 rgba(0,0,0,0)) }
  to   { filter: drop-shadow(0 0 12px rgba(155,93,229,.6)) }
}

/* Î™®Î∞îÏùº ÌÑ∞Ïπò Ï†ëÍ∑ºÏÑ±: Í∏∞Î≥∏ Ìè¨Ïª§Ïä§ ÏïÑÏõÉÎùºÏù∏ Ï†úÍ±∞ */
.zone:focus,
.zone:active,
.start-btn:focus,
.start-btn:active {
  outline: none;
  box-shadow: none;
}
</style>
